// ูุชุญ ุฏูุจ ูููู ุฏุงุฎูู ุฅูู ุงูุชุทุจูู ูุชูููุฐ ุงุชุตุงู USSD ูุจุงุดุฑุฉ
export type SimSlot = 'sim1' | 'sim2';

// ูุชุญ ุฏูุจ ูููู ุฏุงุฎูู ุฅูู ุงูุชุทุจูู ูุชูููุฐ ุงุชุตุงู USSD ูุจุงุดุฑุฉ
import { Capacitor } from '@capacitor/core';
import { AppLauncher } from '@capacitor/app-launcher';

export function openUssdDeepLink(code: string, sim?: SimSlot): void {
  if (!code || !code.trim()) throw new Error('USSD code is required');

  // ุงูุทุฑููุฉ ุงููุณุชูุฑุฉ: ูุชุญ ูุงูุฐุฉ ุงูุงุชุตุงู ุนุจุฑ ูุฎุทุท tel: ูุจุฏุก USSD ุฏุงุฎู ุงูุชุทุจูู
  // ููุดููุฑ ููุท ุงูุฑูุฒ # ุฅูู %23 ูููุจูู * ูุงูุฃุฑูุงู ููุง ูู ุญุชู ูุง ูููุฏ ุชูุงูู USSD
  const telUrl = `tel:${code.replace(/#/g, '%23')}`;
  const encoded = code.replace(/#/g, '%23');
  const deepLinkUrl = `cashy://ussd?code=${encoded}${sim ? `&sim=${sim}` : ''}`;
  let opened = false;

  // ุงููุญุงููุฉ ุงูุฃููู ุนูู ุงูููุตุงุช ุงูุฃุตููุฉ: ุงุณุชุฎุฏุงู ุงูุฏูุจ ูููู ูุถูุงู ุงูุงุชุตุงู ุงููุจุงุดุฑ ุนุจุฑ ACTION_CALL
  try {
    if (Capacitor && Capacitor.getPlatform() !== 'web') {
      AppLauncher.openUrl({ url: deepLinkUrl });
      opened = true;
    }
  } catch (_) { void 0; }

  // ุฎุทุฉ ุนูู ุงูููุจ/ููุจ ููู: ุงุณุชุฎุฏุงู window.location.href
  if (!opened) {
    try {
      window.location.href = deepLinkUrl;
      opened = true;
    } catch (_) { void 0; }
  }

  // ูู ุญุงู ูุดู ุงูุฏูุจ ูููู ูุฃู ุณุจุจุ ูุนูุฏ ุฅูู tel:
  if (!opened) {
    try {
      window.location.href = telUrl;
    } catch (_) { void 0; }
    try {
      if (Capacitor && Capacitor.getPlatform() !== 'web') {
        AppLauncher.openUrl({ url: telUrl });
      }
    } catch (_) { void 0; }
  }
}

// ุชูููุฏ ููุฏ USSD ุญุณุจ ุจุฏุงูุฉ ุฑูู ุงููุญูุธุฉ (010/011/012/015)
export function getUssdCode(phone: string, amount: number): string | null {
  const digits = String(phone || '').replace(/\D/g, '');
  const amt = Math.round(Number(amount || 0));
  if (!digits || digits.length !== 11 || !digits.startsWith('01') || !amt || amt <= 0) {
    return null;
  }
  if (digits.startsWith('010')) return `*9*7*${digits}*${amt}#`; // ููุฏุงููู
  if (digits.startsWith('011')) return `*323*${digits}*${amt}#`; // ุงุชุตุงูุงุช ุญุณุจ ุงููุทููุจ
  if (digits.startsWith('012')) return `*115*1*${digits}*${amt}#`; // ุฃูุฑุงูุฌ ุญุณุจ ุงููุทููุจ
  if (digits.startsWith('015')) return `*322*${digits}*${amt}#`; // WE
  return null;
}

// ุชูููุฏ ููุฏ USSD ุงุนุชูุงุฏูุง ุนูู ุดุจูุฉ ุงูููุฑุณูู (ุงููุญูุธุฉ ุงููุฎุชุงุฑุฉ) ูุน ุงุณุชุฎุฏุงู ุฑูู ุงููุณุชูู ุฏุงุฎู ุงูููุฏ
export function getUssdCodeBySender(senderPhone: string, receiverPhone: string, amount: number): string | null {
  const sender = String(senderPhone || '').replace(/\D/g, '');
  const receiver = String(receiverPhone || '').replace(/\D/g, '');
  const amt = Math.round(Number(amount || 0));
  if (!receiver || receiver.length !== 11 || !receiver.startsWith('01') || !amt || amt <= 0) {
    return null;
  }
  if (!sender || !sender.startsWith('01')) {
    // ุฅู ูู ุชุชููุฑ ุดุจูุฉ ุงูููุฑุณูู ุจุดูู ุตุญูุญ ูุฑุฌุน ุงูููุฏ ุงูุชูููุฏู ุจุงูุงุนุชูุงุฏ ุนูู ุฑูู ุงููุณุชูุจู
    return getUssdCode(receiver, amt);
  }
  if (sender.startsWith('010')) return `*9*7*${receiver}*${amt}#`; // ููุฏุงููู ูููุฑุณูู
  if (sender.startsWith('011')) return `*323*${receiver}*${amt}#`; // ุงุชุตุงูุงุช ูููุฑุณูู ุญุณุจ ุงููุทููุจ
  if (sender.startsWith('012')) return `*115*1*${receiver}*${amt}#`; // ุฃูุฑุงูุฌ ูููุฑุณูู ุญุณุจ ุงููุทููุจ
  if (sender.startsWith('015')) return `*322*${receiver}*${amt}#`; // WE ูููุฑุณูู
  return null;
}

// ุชูููุฏ ููุฏ USSD ูุน ุฅุฏุฎุงู PIN ูู ุงูููุงูุฉ (ุงุฎุชูุงุฑู)
export function getUssdCodeWithPin(phone: string, amount: number, pin: string): string | null {
  const base = getUssdCode(phone, amount);
  const pinDigits = String(pin || '').replace(/\D/g, '');
  if (!base || !pinDigits) return base; // ุฅู ูู ููุฌุฏ PIN ูุนูุฏ ุงูููุฏ ุงูุฃุณุงุณู
  const prefix = base.endsWith('#') ? base.slice(0, -1) : base;
  return `${prefix}*${pinDigits}#`;
}

// ุจูุงุก ููุฏ USSD ูู ุฑูู ุงููุณุชูุจู ููุท ุญุณุจ ุงููุทููุจ ุซู ุงุณุชุจุฏุงู # ูุจู ุงูุงุชุตุงู
export function buildUssdCode(receiver: string, amount: number): string {
  const digits = String(receiver || '').replace(/\D/g, '');
  const amt = Math.round(Number(amount || 0));
  let code = '';
  if (digits.startsWith('010')) {
    // Vodafone
    code = `*9*7*${digits}*${amt}#`;
  } else if (digits.startsWith('011')) {
    // Etisalat
    code = `*323*${digits}*${amt}#`;
  } else if (digits.startsWith('012')) {
    // Orange
    code = `*115*1*${digits}*${amt}#`;
  } else if (digits.startsWith('015')) {
    // WE
    code = `*322*${digits}*${amt}#`;
  }
  return code.replace(/#/g, '%23');
}

// ูุดู ุงููุฒูุฏ ูู ุงูููุฏูุฉ ุซู ุงุฎุชูุงุฑ ุงูุดุฑูุญุฉ ุงูููุงุณุจุฉ ุนุจุฑ ุฅุนุฏุงุฏุงุช ูุงุจูุฉ ููุชุฎุตูุต
export function detectProviderByPrefix(msisdn: string): 'vodafone' | 'etisalat' | 'orange' | 'we' | null {
  const digits = String(msisdn || '').replace(/\D/g, '');
  if (digits.startsWith('010')) return 'vodafone';
  if (digits.startsWith('011')) return 'etisalat';
  if (digits.startsWith('012')) return 'orange';
  if (digits.startsWith('015')) return 'we';
  return null;
}

export function pickSimSlotForMsisdn(msisdn: string): SimSlot {
  const provider = detectProviderByPrefix(msisdn);
  // ูููู ุถุจุท ุงูุดุฑูุญุฉ ุงูููุถูุฉ ููู ูุฒูุฏ ุนุจุฑ localStorageุ ุงูุชุฑุงุถ sim1 ุฅู ูู ูุญุฏุฏ
  const key = provider ? `simSlot_${provider}` : 'simSlot_default';
  try {
    const saved = localStorage.getItem(key);
    if (saved === 'sim2') return 'sim2';
  } catch { void 0; }
  return 'sim1';
}

// ุฅุฑุณุงู ุชุญููู ุนุจุฑ ุฏูุจ ูููู ุจุนุฏ ุงุฎุชูุงุฑ ุงูุดุจูุฉ ุชููุงุฆููุง ูู ุฑูู ุงููุญูุธุฉ
export function sendTransferDeepLink(receiver: string, amount: number): void {
  const code = getUssdCode(receiver, amount);
  if (!code) {
    throw new Error('Unsupported network or invalid receiver/amount');
  }
  const sim = pickSimSlotForMsisdn(receiver);
  openUssdDeepLink(code, sim);
}

// ุฅุฑุณุงู ุชุญููู ุนุจุฑ ุฏูุจ ูููู ุจุงุนุชูุงุฏ ุดุจูุฉ ุงูููุฑุณูู (ุงููุญูุธุฉ ุงููุฎุชุงุฑุฉ)
export function sendTransferFromWalletDeepLink(senderPhone: string, receiver: string, amount: number): void {
  const code = getUssdCodeBySender(senderPhone, receiver, amount);
  if (!code) {
    throw new Error('Unsupported network or invalid sender/receiver/amount');
  }
  const sim = pickSimSlotForMsisdn(senderPhone || receiver);
  openUssdDeepLink(code, sim);
}

// ุฅุธูุงุฑ ุฅุดุนุงุฑ ุนุจุฑ ูุงุฌูุฉ ุงูููุจ ุงูููุงุณูุฉ ุจุฏููุง ูู ุงูุจูุฌู
export async function showNativeNotification(message: string, title = 'Cashy'): Promise<void> {
  if (!message || !message.trim()) throw new Error('Notification message is required');
  try {
    if (typeof Notification !== 'undefined') {
      const permission = Notification.permission === 'granted'
        ? 'granted'
        : await Notification.requestPermission();
      if (permission === 'granted') {
        new Notification(title || 'Cashy', { body: message });
        return;
      }
    }
    // Fallbacks
    if (typeof window !== 'undefined' && typeof alert === 'function') {
      alert(`${title || 'Cashy'}\n${message}`);
    } else {
      console.log(`[${title || 'Cashy'}] ${message}`);
    }
  } catch (err) {
    console.warn('Notification not available:', err);
  }
}